export default class Frame {
  constructor(state) {
    this.state = state;
    const {
      currentResolution, canvas, ctx, COLORS,
    } = state;
    this.COLORS = COLORS;
    this.canvas = canvas;
    this.ctx = ctx;
    const { cols, rows } = currentResolution;
    this.cols = cols;
    this.rows = rows;
    this.pixelSize = canvas.width / this.cols;
    this.pixels = Array(cols * rows).fill(COLORS.transparent);

    const newFrame = document.createElement('div');
    newFrame.className = 'frame';
    newFrame.id = `frame${state.frames.length}`;
    newFrame.innerHTML = `
    <div class="frame-number" id="number">${state.frames.length + 1}</div>
    <img class="frame-preview" id="frame-preview${state.frames.length}"
         src="" alt="preview"></img>

    <div class="copy-frame" id="copy"></div>
    `;
    const frameControl = document.getElementById('frame-control');
    const newFrameButton = document.getElementById('new-frame');
    frameControl.insertBefore(newFrame, newFrameButton);
    newFrame.addEventListener('click', state.switchFrame);
  }

  static clone(original) {
    if (original instanceof Frame) {
      const copy = new Frame(original.state);
      Object.assign(copy.pixels, original.pixels);

      return copy;
    }
    throw new Error('Object trying to be cloned is not a Frame');
  }

  draw() {
    // clean canvas
    this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);

    // draw each pixel
    for (let i = 0; i < this.cols; i += 1) {
      for (let j = 0; j < this.rows; j += 1) {
        this.ctx.fillStyle = this.pixels[i * this.rows + j];
        this.ctx.strokeStyle = this.pixels[i * this.rows + j];
        this.ctx.fillRect(this.pixelSize * j, this.pixelSize * i, this.pixelSize, this.pixelSize);
      }
    }

    // make preview
    this.state.previews[this.state.currentFrame] = this.canvas.toDataURL();
    document.getElementById(`frame-preview${this.state.currentFrame}`).src = this.state.previews[this.state.currentFrame];
  }

  reset() {
    // eslint-disable-next-line no-unused-vars
    this.pixels = this.pixels.map(() => this.COLORS.transparent);
    this.draw();
  }

  getClickedPixel(x, y, exeption) {
    const xNum = Math.floor(x / this.pixelSize);
    const yNum = Math.floor(y / this.pixelSize);
    const index = yNum * this.cols + xNum;
    if (index !== exeption && xNum >= 0 && yNum >= 0
        && xNum < this.cols && yNum < this.rows) {
      return { indx: index, x: xNum, y: yNum };
    }
    return { indx: undefined, x: undefined, y: undefined };
  }

  outOfRange(x, y) {
    if (x < 0 || y < 0 || x >= this.cols || y >= this.rows) return true;
    return false;
  }

  fill(x, y, initialColor) {
    const { color } = this.state;
    // center
    if (!this.outOfRange(x, y) && initialColor !== this.pixels[y * this.cols + x]) {
      // top
      if (!this.outOfRange(x, y - 1) && this.pixels[(y - 1) * this.cols + x] === initialColor) {
        this.pixels[(y - 1) * this.cols + x] = color;
        this.fill(x, y - 1, initialColor);
      }
      // right
      if (!this.outOfRange(x + 1, y) && this.pixels[y * this.cols + (x + 1)] === initialColor) {
        this.pixels[y * this.cols + (x + 1)] = color;
        this.fill(x + 1, y, initialColor);
      }
      // bottom
      if (!this.outOfRange(x, y + 1) && this.pixels[(y + 1) * this.cols + x] === initialColor) {
        this.pixels[(y + 1) * this.cols + x] = color;
        this.fill(x, y + 1, initialColor);
      }
      // left
      if (!this.outOfRange(x - 1, y) && this.pixels[y * this.cols + x - 1] === initialColor) {
        this.pixels[y * this.cols + x - 1] = color;
        this.fill(x - 1, y, initialColor);
      }
    }
  }

  click(event) {
    const { tool, color } = this.state;

    const canvasPos = this.canvas.getBoundingClientRect();
    const x = event.clientX - canvasPos.left;
    const y = event.clientY - canvasPos.top;
    const pixel = this.getClickedPixel(x, y);
    const pixelIdex = pixel.indx;
    if (pixelIdex || pixelIdex === 0) {
      if (tool === 'pencil' || tool === 'eraser') {
        this.pixels[pixelIdex] = color;
      }
      if (tool === 'bucket') {
        const initialColor = this.pixels[pixelIdex];
        this.pixels[pixelIdex] = color;
        this.fill(pixel.x, pixel.y, initialColor);
      }
      if (tool === 'picker') {
        this.state.changeColor(this.pixels[pixelIdex]);
      }
    }
    this.draw();
  }

  mouseDown(event) {
    const { tool, color } = this.state;

    if (tool === 'pencil' || tool === 'eraser') {
      const canvasPos = this.canvas.getBoundingClientRect();
      const x = event.clientX - canvasPos.left;
      const y = event.clientY - canvasPos.top;
      this.pixel = this.getClickedPixel(x, y);
      this.pixelIndex = this.pixel.indx;
      if (this.pixelIndex || this.pixelIndex === 0) {
        this.initialX = x;
        this.initialY = y;
        this.pixels[this.pixelIdex] = color;
        document.onmousemove = this.onMove.bind(this);
        document.onmouseup = this.onUp.bind(this);
      }
    }
    this.draw();
  }

  onMove(eventMove) {
    const { tool, color } = this.state;

    if (tool === 'pencil' || tool === 'eraser') {
      const canvasPos = this.canvas.getBoundingClientRect();
      const x = eventMove.clientX - canvasPos.left;
      const y = eventMove.clientY - canvasPos.top;
      const pixel = this.getClickedPixel(x, y);
      const pixelIdex = pixel.indx;
      if (pixelIdex || pixelIdex === 0) {
        this.pixels[pixelIdex] = color;
      }
      this.drawLine(x, y);
      this.initialX = x;
      this.initialY = y;
      this.draw();
    }
  }

  drawLine(x, y) {
    const { color } = this.state;

    let curX = x;
    let xIncrement = Math.abs(curX - this.initialX) / this.cols;
    if (curX > this.initialX) { xIncrement = -xIncrement; }
    let curY = y;
    let yIncrement = Math.abs(curY - this.initialY) / this.rows;
    if (curY > this.initialY) { yIncrement = -yIncrement; }
    while (Math.abs(curX - this.initialX) > Math.abs(xIncrement)
        || Math.abs(curY - this.initialY) > Math.abs(yIncrement)) {
      curX += xIncrement;
      curY += yIncrement;
      const curFig = this.getClickedPixel(curX, curY).indx;
      if (curFig) {
        this.pixels[curFig] = color;
      }
    }
  }

  onUp() {
    document.onmousemove = null;
    document.onmouseup = null;
    this.draw();
  }
}
